/* ============================
   Arcade Theme  (body.theme-arcade)
   ============================ */

/* Pixel/arcade headline */
@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");

/* ---- Scene background: dark nebula + faint grid ---- */
body.theme-arcade {
  color: #cdeaff;
  background:
    /* soft vignettes */
    radial-gradient(1200px 600px at 20% -10%, rgba(46,73,120,.35), transparent 60%),
    radial-gradient(1000px 600px at 110% 10%, rgba(16,26,56,.35), transparent 60%),
    /* subtle grid */
    linear-gradient(rgba(255,255,255,.06) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255,255,255,.06) 1px, transparent 1px),
    /* base */
    #0b1220;
  background-size:
    100% 100%,
    100% 100%,
    24px 24px,
    24px 24px,
    auto;
  background-position:
    center center,
    center center,
    0 0,
    0 0,
    0 0;
}

/* ---- Cards/chrome placed on the dark field ---- */
body.theme-arcade .chrome-card,
body.theme-arcade .panel,
body.theme-arcade .box,
body.theme-arcade .help-card {
  background: rgba(12,19,35,.72);
  border: 1px solid rgba(255,255,255,.12);
  border-radius: 14px;
  backdrop-filter: blur(3px);
  box-shadow: 0 12px 28px rgba(0,0,0,.45);
  color: #cdeaff;
}

/* ---- Title stack ---- */
body.theme-arcade .app-title {
  margin: 0;
  font-family: "Press Start 2P", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  font-size: 24px;
  line-height: 1.2;
  letter-spacing: 1px;
  color: #75e0ff;
  text-shadow:
    0 0 8px rgba(117,224,255,.55),
    0 0 18px rgba(117,224,255,.35);
}

body.theme-arcade .version-badge {
  display: inline-block;
  margin-top: 6px;
  padding: 4px 10px;
  background: rgba(255,255,255,.08);
  color: #cdeaff;
  border: 1px solid rgba(255,255,255,.23);
  border-radius: 999px;
}

/* ---- Controls / selects / buttons ---- */
body.theme-arcade .select,
body.theme-arcade .btn,
body.theme-arcade button {
  color: #e6f6ff;
  background: #0f2136;
  border: 1px solid #2a4266;
  border-radius: 10px;
  padding: 8px 12px;
  transition: transform .05s ease, box-shadow .12s ease, border-color .12s ease;
}
body.theme-arcade .btn:hover,
body.theme-arcade button:hover,
body.theme-arcade .select:hover {
  transform: translateY(-1px);
  border-color: #3b5c8b;
  box-shadow: 0 8px 18px rgba(0,0,0,.35);
}

/* ---- Theme switch active state ---- */
body.theme-arcade .theme-switch .theme-btn.is-active {
  box-shadow: 0 0 0 2px rgba(117,224,255,.45) inset,
              0 0 12px rgba(117,224,255,.25);
  background: rgba(117,224,255,.10);
}

/* ---- Legend, smaller text and neon-ish swatches ---- */
body.theme-arcade .legend-row { font-size: .9rem; }
body.theme-arcade .legend-swatch { border: 1px solid rgba(255,255,255,.22); }
body.theme-arcade .legend-yellow { background: rgba(255,214,102,.65); }
body.theme-arcade .legend-red    { background: rgba(255,112,143,.65); }
body.theme-arcade .legend-blue   { background: rgba(120,190,255,.55); }

/* ---- Stats chips readable on dark (belt & suspenders;
   custom.css also handles this globally) ---- */
body.theme-arcade .stat-chip,
body.theme-arcade .bg-gray-100,
body.theme-arcade .bg-green-100,
body.theme-arcade .bg-blue-100 {
  background: rgba(255,255,255,.08) !important;
  border: 1px solid rgba(255,255,255,.23) !important;
  color: #cdeaff !important;
}

/* ---- Editor text ---- */
body.theme-arcade .editor-content {
  color: #e9f3ff;
}

/* ---- Highlights (neon but readable) ---- */
body.theme-arcade mark[data-highlight-color="yellow"] {
  background-color: rgba(255,214,102,.30);
  color: #ffffff;
  border: 1px solid rgba(255,214,102,.75);
  border-radius: 6px;
  padding: 0 .08em;
}
body.theme-arcade mark[data-highlight-color="red"] {
  background-color: rgba(255,112,143,.30);
  color: #ffffff;
  border: 1px solid rgba(255,112,143,.75);
  border-radius: 6px;
  padding: 0 .08em;
}
body.theme-arcade mark[data-highlight-color="blue"] {
  background-color: rgba(120,190,255,.28);
  color: #ffffff;
  border: 1px solid rgba(120,190,255,.65);
  border-radius: 6px;
  padding: 0 .08em;
}

/* ---- Hard-word underline (stays visible above marks) ---- */
body.theme-arcade .editor-content span[data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #cfa0ff;
  padding-bottom: 1px;
}

/* ---- Tooltip on dark ---- */
body.theme-arcade #ss-tooltip {
  background: #0b1626 !important;
  color: #e9f3ff !important;
  border: 1px solid #2a4266 !important;
  border-radius: 10px !important;
  box-shadow: 0 10px 24px rgba(0,0,0,.35) !important;
}

/* ---- Focus ring ---- */
body.theme-arcade *:focus-visible {
  outline: 2px solid #75e0ff;
  outline-offset: 2px;
}
