/* === Dog Park theme, applies when <body> has .theme-dog ================== */
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@600;800&display=swap");

:root {
  --dog-navy: #08111c;
  --dog-deep: #0e1a2a;
  --dog-cyan: #35d0ff;
  --dog-yellow: #ffe27a;
  --dog-pink: #ff6b8b;
  --dog-lime: #7ef29a;
  --dog-teal: #29c7a9;
  --dog-ink: #e8f0ff;
}

/* background, pattern tiled with a dark overlay so text stays readable */
body.theme-dog {
  color: var(--dog-ink);
  font-family: "Nunito", system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
  background-image:
    linear-gradient(rgba(8,17,28,.88), rgba(8,17,28,.88)),
    url("./assets/dog-bg.jpg");
  background-repeat: no-repeat, repeat;
  background-size: cover, 720px auto;
  background-position: center top, 0 0;
}

/* cards and editor surface */
body.theme-dog .editor-content,
body.theme-dog .help-card,
body.theme-dog .panel,
body.theme-dog .box {
  background: rgba(14,26,42,.78);
  border: 1px solid rgba(255,255,255,.08);
  border-radius: 14px;
  box-shadow: 0 10px 28px rgba(0,0,0,.25);
  color: var(--dog-ink);
}

/* title and version */
body.theme-dog .app-title {
  margin: 0;
  color: var(--dog-yellow);
  text-shadow: 0 2px 0 rgba(0,0,0,.35);
  letter-spacing: .3px;
  font-weight: 800;
}
body.theme-dog .version-badge {
  display: inline-block;
  margin-top: 6px;
  padding: 3px 10px;
  background: rgba(53,208,255,.14);
  color: #c6eeff;
  border: 1px solid rgba(53,208,255,.35);
  border-radius: 999px;
}

/* controls */
body.theme-dog select,
body.theme-dog .btn,
body.theme-dog button,
body.theme-dog .toggle {
  color: var(--dog-ink);
  background: #0f2136;
  border: 1px solid #2a4266;
  border-radius: 10px;
  padding: 8px 12px;
  transition: transform .05s ease, box-shadow .12s ease, border-color .12s ease;
}
body.theme-dog button:hover,
body.theme-dog .btn:hover,
body.theme-dog select:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(0,0,0,.25);
  border-color: #3c5c8b;
}

/* theme switch group */
body.theme-dog .theme-switch .theme-btn.is-active {
  box-shadow: 0 0 0 2px var(--dog-yellow) inset;
  background: rgba(255,226,122,.12);
}

/* legend squares */
body.theme-dog .legend-yellow,
body.theme-dog .legend-red,
body.theme-dog .legend-blue {
  width: 14px; height: 14px;
  border-radius: 4px; display: inline-block;
  border: 1px solid rgba(255,255,255,.15);
}
body.theme-dog .legend-yellow { background: rgba(255,226,122,.65); }
body.theme-dog .legend-red    { background: rgba(255,107,139,.65); }
body.theme-dog .legend-blue   { background: rgba(120,190,255,.55); }

/* sentence highlights, readable text */
body.theme-dog mark[data-highlight-color="yellow"] {
  background-color: rgba(255,226,122,.35);
  color: #ffffff;
  border: 1px solid #ffd463;
  border-radius: 6px; padding: 0 .08em;
}
body.theme-dog mark[data-highlight-color="red"] {
  background-color: rgba(255,107,139,.35);
  color: #ffffff;
  border: 1px solid #ff6b8b;
  border-radius: 6px; padding: 0 .08em;
}
body.theme-dog mark[data-highlight-color="blue"] {
  background-color: rgba(120,190,255,.30);
  color: #ffffff;
  border: 1px solid rgba(120,190,255,.65);
  border-radius: 6px; padding: 0 .08em;
}

/* hard word underline */
body.theme-dog .editor-content span[data-hard-word="1"] {
  text-decoration: none;
  border-bottom: 2px dotted #cfa0ff;
  padding-bottom: 1px;
}

/* tooltip */
body.theme-dog #ss-tooltip {
  background: #0b1626 !important;
  color: #e9f3ff !important;
  border: 1px solid #2a4266 !important;
  border-radius: 10px !important;
  box-shadow: 0 10px 24px rgba(0,0,0,.35) !important;
}

/* focus ring */
body.theme-dog *:focus-visible {
  outline: 2px solid var(--dog-cyan);
  outline-offset: 2px;
}

/* readable chrome on busy dog tile */
body.theme-dog .hud {
  background: rgba(8,17,28,.78);
  border: 1px solid rgba(255,255,255,.10);
  border-radius: 16px;
  padding: 14px;
  margin: 10px 0 14px;
  box-shadow: 0 12px 28px rgba(0,0,0,.35);
  backdrop-filter: blur(3px);
}
body.theme-dog .legend-row,
body.theme-dog .top-controls { gap: 10px; }

body.theme-dog .chrome-card {
  background: rgba(8, 17, 28, .78);
  border: 1px solid rgba(255,255,255,.10);
  box-shadow: 0 12px 28px rgba(0,0,0,.35);
}
