/* ====== Base layout ====== */
* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  background: #f8fafc;
  color: #111827;
  font: 16px/1.45 system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
}
.container {
  max-width: 980px;
  margin: 24px auto 56px;
  padding: 0 16px;
}

/* ====== Title / HUD ====== */
.hud {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
  padding: 14px 16px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  background: #ffffff;
}
.title-stack {
  display: flex;
  flex-direction: column;
  gap: 6px;
}
.app-title {
  margin: 0;
  font-size: 28px;
  font-weight: 800;
  letter-spacing: .2px;
}
.version-badge {
  display: inline-block;
  font-size: 13px;
  padding: 4px 10px;
  background: #eef2ff;
  color: #4338ca;
  border: 1px solid #c7d2fe;
  border-radius: 999px;
}

/* Theme switch lives at the right edge */
.theme-switch { margin-left: auto; display: flex; gap: 8px; }
.theme-btn {
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  background: #fff;
  border-radius: 10px;
  cursor: pointer;
}
.theme-btn.is-active {
  border-color: #6366f1;
  box-shadow: 0 0 0 2px rgba(99,102,241,.15);
}

/* ====== Controls row ====== */
.top-controls {
  margin-top: 12px;
  padding: 12px 16px;
  border-radius: 12px;
  border: 1px solid #e5e7eb;
  background: #ffffff;

  display: grid;
  grid-template-columns: auto auto 1fr auto;
  gap: 12px 16px;
  align-items: center;
}
.label { font-weight: 600; }
.select {
  height: 36px; padding: 0 10px;
  border: 1px solid #d1d5db; border-radius: 10px; background: #fff;
}

/* Legend smaller/clean */
.legend-row { font-size: .9rem; display: flex; gap: 16px; align-items: center; }
.legend-swatch { width: 14px; height: 14px; display: inline-block; border-radius: 4px; margin-right: 6px; }
.legend-yellow { background: #fefcbf; border: 1px solid #f5e17d; }
.legend-red    { background: #fecaca; border: 1px solid #f5a3a3; }
.legend-blue   { background: #dbeafe; border: 1px solid #bcd9ff; }

/* Hard words toggle */
.hardwords-toggle { margin-left: auto; user-select: none; }
.hardwords-toggle input { margin-right: 6px; }
.hardwords-toggle .aa { font-weight: 700; margin: 0 4px 0 2px; }

/* ====== Stats ====== */
.stats-row { display: flex; gap: 10px; margin: 12px 0 10px; }
.stat-chip {
  display: inline-block;
  padding: 6px 10px;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  background: #f3f4f6;
  color: #111827;
}

/* ====== Editor panel ====== */
.panel.editor-wrap {
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  background: #fff;
  padding: 0;
}

/* The Tiptap editable region */
.editor-content {
  min-height: 320px;              /* multi-line by default */
  padding: 16px;
}

/* Placeholder readability */
.editor-content .is-editor-empty p:first-child::before {
  color: #9aa3af;
}

/* ====== Highlights (global defaults) ====== */
mark[data-highlight-color="yellow"] {
  background-color: #fefcbf;
  color: #1f2937;
  border: 1px solid #f5e17d;
  border-radius: 6px;
  padding: 0 .08em;
}
mark[data-highlight-color="red"] {
  background-color: #fecaca;
  color: #1f2937;
  border: 1px solid #f5a3a3;
  border-radius: 6px;
  padding: 0 .08em;
}
mark[data-highlight-color="blue"] {
  background-color: #dbeafe;
  color: #1f2937;
  border: 1px solid #bcd9ff;
  border-radius: 6px;
  padding: 0 .08em;
}

/* ====== Chrome card helper (used by themes too) ====== */
.chrome-card { background: #fff; border: 1px solid #e5e7eb; border-radius: 12px; }

/* ====== Arcade: readable chips on dark ====== */
body.theme-arcade .stat-chip,
body.theme-arcade .bg-gray-100,
body.theme-arcade .bg-green-100,
body.theme-arcade .bg-blue-100 {
  background: rgba(255,255,255,.08) !important;
  border: 1px solid rgba(255,255,255,.23) !important;
  color: #cdeaff !important;
}
